/**
 * 获取地理位置
 * @param isApp
 * @param isWechat
 * @author  yinshi
 * @date 16/11/21.
 * @return {Promise}
 */
import {app} from './common/app';
import {h5} from './common/h5';
import {weixin} from  './common/weixin';
import {amap} from './common/amap';
export function getLocation(type,options = {}) {
   return new Promise(function (resolve,reject) {
        switch (type){
            case 'app':
                app(resolve);
                break;
            case 'wechat':
                weixin(resolve,reject,options)
                break;
            default:
                if( window.AMap && window.AMap.Map){
                    amap(resolve, reject, options);
                }else {
                    h5(resolve,reject,options);
                }

        }
    })
}



