/**
 * @description
 * @author  yinshi
 * @date 16/12/14.
 */
export function weixin(success,error,options) {
    if(!wx){
        alert('您没有引入wxJSSDK')
        return
    }
    const _opts = options || {}

    _opts.type = _opts.type || 'wgs84';
    _opts.debug = _opts.debug || false;
    wx.ready(() => {
        wx.getLocation({
            type: _opts.type,
            success(res) {
                _opts.debug && alert(res)
                success(res);
            },
            fail(err) {
                _opts.debug && alert(err)
                error(err);
            },
            cancel(err){
                _opts.debug && alert(err);
                error(err);
            }
        });
    });
}


