/**
 * share
 * version 3.2
 */
module.exports = function share(opt,callback){
    let bridge = window.bridge;
    bridge.registerHandler('shareCallback', function(ret){
        if(callback){
            callback(bridge.toJson(ret));
        }
    });
    opt.method = 'shareCallback';
    bridge.callHandler('share', opt);
}


